@import url('https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400;600;700;800&display=swap');

* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family: 'Open Sans', sans-serif;
}

.App {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100vh;
}

.about-me {
    font-size: 1.15rem;
    margin: 50px 50px;
    padding: 0 20px 0 20px;
    white-space: normal;
    color: #617d98;
}

.carousel {
    overflow: hidden;
    width: 700px;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.inner {
    white-space: nowrap;
    transition: transform 0.3s;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.05);
}

.carousel-item {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    height: 700px;
    background-color: #fff;
}

.carousel-item-text {
    font-size: 1.15rem;
    margin: 50px 50px;
    padding: 0 20px 0 20px;
    white-space: normal;
    color: #617d98;
}

.carousel-img {
    width: 300px;
    padding: 0 20px 0 20px;
    position: absolute;
    margin-bottom: 500px;
}

.carousel-link {
    position: absolute;
    bottom: 0;
    margin-bottom: 50px;
}

.carousel-buttons {
    display: flex;
    justify-content: space-evenly;
}

.carousel-title {
    position: absolute;
    top: 0;

}

.button-arrow {
    background: none;
    border: none;
    cursor: pointer;
    margin-top: 20px;
}

.indicators {
    display: flex;
    justify-content: space-around;
    align-items: center;
    margin-top: 20px;
}

.indicator-buttons {
    border: none;
    cursor: pointer;
}

.indicators>button {
    margin: 5px;
    background: none;
}

.indicator-symbol {
    color: #26343f;
}

.indicator-symbol-active {
    color: #ffffff;
}

.home-link {
    position: absolute;
    bottom: 0;
    margin-bottom: 50px;
}

.name {
    position: absolute;
    top: 0;
    margin-top: 200px;
}

.page {
    flex-basis: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: margin-left 1s;
}

.page-with-navbar {
    margin-left: 15%;
}

@media only screen and (max-width: 768px) {
    .page-with-navbar {
        width: 100%;
        margin-left: 0;
    }

}